<template>
<div v-scroll="onscroll">
    <nav>
        <ul>
            <li class="hidden"><a href="#bio">Bio</a></li>
            <li class="hidden"><a href="#activities">Activities</a></li>
            <li class="hidden"><a href="#skills">Skills</a></li>
            <li class="hidden"><a href="#projects">Projects</a></li>
        </ul>
    </nav>
    <img src="../assets/ImageAssets/header.jpg" alt="Header Image"  id="header-img">
    <h1 id="heading" class="hidden">Olivia Lai</h1>
</div>
</template>

<script>
import $ from 'jquery'
export default {
    name: 'Header',
    mounted() {
        window.setTimeout(function() {
            $("#header-img").addClass('dim');
            $('#heading').removeClass('hidden');
            $('#heading').addClass('brighten');
        }, 500);

        window.setTimeout(function() {
            let links = document.querySelectorAll('nav ul li');
            links.forEach((link, index) => {
                link.classList.add('fade');
                link.classList.remove('hidden');
                link.style.animationDuration = index+'s';
            });
        }, 1500);

    },
    methods: {
        onscroll () {
            let windowY = window.screenY;

            let image = $("#header-img");

            let imgY = windowY - this.coords(image[0]).y;
            image[0].style.transform = `translate(-50%,${imgY}px)`;
        },
        coords (el) {
          let coords = el.getBoundingClientRect();
          return {
            y: coords.top / 2
          }
        }
    }
}
</script>

