        opacity: .4;
    }
}

.dim {
    opacity: .4;
    animation-name: dimmer;
    animation-duration: 1s;
}

@keyframes fadeIn {
    from {
        opacity: 0;
        transform: translateX(-50px);
    }
    to {
        opacity: 1;
        transform: translateX(0px);
    }
}

.fade {
    opacity: 1;
    animation: fadeIn;
}

#header-img {
    height: 100vh;
    left: 50%;
    transform: translateX(-50%);
    display: block;
    position: relative;
}

#heading {
    font-weight: lighter;
    color: white;
    text-align: center;
    display: block;
    position: absolute;
    top: 50vh;
    left: 50%;
    transform: translate(-50%, -50%);
}

.hidden {
    opacity: 0;
}

.brighten {
    opacity: 1;
    transition: ease-in-out 1s;
}

nav {
    display: block;
    position: absolute;
    left: 50%;
    top: 10vh;
    transform: translate(-50%, -50%);
    width: 100%;
    z-index: 2;
}

nav ul li {
    display: inline;
    transition: ease-in-out .25s;
}

nav ul li a {
    text-decoration: none;
