              <v-btn icon @click="changeCard(showB, 'tennis');">
                <v-icon>{{ showB.show ? 'mdi-chevron-down' : 'mdi-chevron-up' }}</v-icon>
              </v-btn>
            </v-card-actions>
            <div class="img-sect">
              <v-img src="../assets/ImageAssets/RedTennis.png" width="100%" vertical-align="middle"></v-img>
            </div>
            <div class="tennis card-block hidden slideUp">
              <div class="tennis-text card-block-text hidden fadeOut">
                <h6>Tennis</h6>
                <p>Girls Varsity</p>
                <p>Doubles</p>
