        <div class="item c">
          <v-card dark elevation="24" class="mx-auto" width="800px" style="position:relative">
            <v-card-actions class="justify-center">
              <v-btn icon @click="changeCard(showC, 'dance');">
                <v-icon>{{ showC.show ? 'mdi-chevron-down' : 'mdi-chevron-up' }}</v-icon>
              </v-btn>
            </v-card-actions>
            <div class="img-sect">
              <v-img src="../assets/ImageAssets/RedDance.png" width="100%" vertical-align="middle"></v-img>
            </div>
            <div class="dance card-block hidden slideUp">
              <div class="dance-text card-block-text hidden fadeOut">
                <h6>Dance</h6>
                <p>8 years of Experience</p>
                <p>MVHS Dance Spectrum</p>
                <p>4th Period - Advanced</p>
                <h6>Genres</h6>
                <p>Contemporary</p>
                <p>Jazz</p>
                <p>Hip Hop</p>
                <p>Tap</p>
              </div>
            </div>
