    </div>
    <div class="center">
      <div class="prev">Previous</div>
      <div class="next">Next</div>
    </div>
  </div>
</template>

<script>
import $ from "jquery";
export default {
  data: () => {
    return {
      showA: {
        show: false,
      },
      showB: {
        show: false,
      },
      showC: {
        show: false,
      },
    };
  },
  mounted() {
    var carousel = $(".carousel"),
      currdeg = 0;

    $(".next").on(
      "click",
      {
        d: "n",
      },
      rotate
    );
    $(".prev").on(
      "click",
      {
        d: "p",
      },
      rotate
    );

    function rotate(e) {
      if (e.data.d == "n") {
        currdeg = currdeg - 120;
      }
      if (e.data.d == "p") {
        currdeg = currdeg + 120;
      }
      carousel.css({
        "-webkit-transform": "rotateY(" + currdeg + "deg)",
        "-moz-transform": "rotateY(" + currdeg + "deg)",
        "-o-transform": "rotateY(" + currdeg + "deg)",
        transform: "rotateY(" + currdeg + "deg)",
      });
    }
  },
  methods: {
    changeCard(showObj, activity) {
      let activityClass = "." + activity;
      let activityText = "." + activity + "-text";

      if (showObj.show) {
        $(activityText).toggleClass("fadeIn");
        $(activityText).toggleClass("fadeOut");
        window.setTimeout(() => {
          $(activityClass).toggleClass("slideDown");
          $(activityClass).toggleClass("slideUp");
          $(activityText).toggleClass("hidden");
        }, 750);
        window.setTimeout(() => {
          $(activityClass).toggleClass("hidden");
        }, 1500);
      } else {
        $(activityClass).toggleClass("slideDown");
        $(activityClass).toggleClass("slideUp");
        $(activityClass).toggleClass("hidden");
        window.setTimeout(() => {
          $(activityText).toggleClass("fadeIn");
          $(activityText).toggleClass("fadeOut");
        }, 750);
        window.setTimeout(() => {
          $(activityText).toggleClass("hidden");
        }, 750);
      }

      showObj.show = !showObj.show;
    },
  },
};
</script>

