import "chartjs-plugin-deferred";

var xLabels = {
    1: 'Beginner',
    5: 'Amateur',
    9: 'Competent'
}

var skillsChartData = {
    animating: true,
    responsive: true,
    type: 'horizontalBar',
    data: {
        labels: ['HTML', 'CSS', 'Javascript', 'JQuery', 'Bootstrap', 'Python', 'Java', 'ExpressJS', 'Vue', 'MongoDB', 'SQL', 'Flutter', 'SASS'],
        datasets: [{ // one line graph
            data: [10, 9.5, 8.25, 8.25, 7.5, 7, 6.5, 5, 4.5, 4, 4, 3.75, 3.5],
            backgroundColor: [
                '#FC3697',
                '#FB3C93',
                '#FB4390',
                '#FB498D',
                '#FB508A',
                '#FB5686',
                '#FB5D83',
                '#FB6480',
                '#FB6A7D',
                '#FB7179',
                '#FB7776',
                '#FB7E73',
                '#FB8570',
            ],
            borderWidth: 0
        }]
    },
    options: {
        tooltips: {
            enabled: false,
            callbacks: {
                label: function(tooltipItem) {
                       return tooltipItem.yLabel;
                }
             }
        },
        responsive: true,
        lineTension: 1,
        scales: {
            yAxes: [{
                ticks: {
                    beginAtZero: true,
                    padding: 0,
                    fontColor: 'white',
                    fontFamily: 'Quicksand',
                    fontSize: changeFont()
                }
            }],
            xAxes: [{
                ticks: {
                    beginAtZero: true,
                    fontColor: 'white',
                    fontFamily: 'Quicksand',
                    fontSize: 14,
                    callback: function (value) {
                        return xLabels[value];
                    }
                },
            }]
        },
        legend: {
            display: false,
        },
        plugins: {
            deferred: {
                yOffset: 100,
                delay: 500
            }
        },
        animation: {
            duration: 2500
        }
    }
}

function changeFont () {
    if (window.innerWidth <= 600){
        return 10;
    }
    return 16;
}

export default skillsChartData;
