<template>
  <div>
    <h1 id="skills">Skills</h1>
    <div id="chart">
      <canvas id="skills-chart"></canvas>
    </div>
  </div>
</template>

<script>
import Chart from "chart.js";
import skillsChartData from "../js/chart-data.js";

export default {
  data() {
    return {
      skillsChartData: skillsChartData,
    };
  },
  mounted() {
    this.createChart("skills-chart", this.skillsChartData);
  },
  methods: {
    createChart(chartId, chartData) {
      const ctx = document.getElementById(chartId);
      const myChart = new Chart(ctx, {
        type: chartData.type,
        data: chartData.data,
        options: chartData.options,
      });
      return myChart;
    },
  },
};
</script>

<style lang="css">
h1 {
  padding-top: 15vh;
  padding-bottom: 10vh;
}

@media (min-width: 500px) {
  #chart {
    width: 70%;
    margin: auto;
    margin-bottom: 15vh;
  }
}

@media (max-width: 600px) {
  #chart {
    width: 90%;
    margin: auto;
    margin-bottom: 15vh;
  }
}
</style>
